export default {
    data() {
        return {
            submitSuccess: false,
            submitError: false,
            submitText: 'Uložené!',
            id: null,
            name: null,
            surname: null,
            nickname: null,
            email: null,
            level: 100,
            created_at: null,
            updated_at: null,
            deleted_at: null,
            created_by: null,
            updated_by: null,
            deleted_by: null,
        }
    },
    methods: {
        async submitForm() {
                await this.$axios.$post('/user', {
                    "name": this.name,
                    "surname": this.surname,
                    "nickname": this.nickname,
                    "email": this.email,
                    "level": this.level,
                })
                .then((response) => {
                    console.log(response);
                    this.submitSuccess = true
                    this.submitText = 'Uložené!'
                    this.name = ""
                    this.surname = ""
                    this.nickname = ""
                    this.email = ""
                    this.level = 100
                    setTimeout(() => this.submitSuccess = false, 4000)
                    // this.$router.push('/admin/tags/detail/'+ response.id)
                    this.$router.push('/admin/users')
                    this.name = ""
                }, (error) => {
                    console.log(error);
                });
        },
    },
}
